.container {
  width: 100%;
  margin-top: 10vh;
  box-sizing: border-box;
}
.wrapper {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  box-sizing: border-box;
}
.leftSectionWrapper {
  display: flex;
  flex-direction: column;
  width: 50%;
}
.section {
  line-height: 1.5;
  font-size: 27px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  padding: 10px;
}
.secText {
  font-size: 26.4px;
}
.lorem {
  width: 70%;
  box-sizing: border-box;
}
.lorem p {
  opacity: 0.3;
  font-size: 13px;
  padding: 10px;
}

.image {
  display: flex;
  position: absolute;
  left: 48vw;
  z-index: 3;
  justify-content: space-between;
  width: 18%;
}

.image img {
  width: 230px;
  height: 30vh;
  background-color: rgb(0, 0, 255);
  border-radius: 20px;
}

.bigImg {
  transform: translate(25%, -24%);
}
.bigImg img {
  background-color: rgb(0, 0, 255);
  border-radius: 30px;
  border-top-right-radius: 0;
  border-top-right-radius: 0;
  width: 30vw;
  height: 40vh;
}

@media only screen and (max-width: 500px) {
  .wrapper {
    flex-direction: column;
  }
  .leftSectionWrapper {
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex: 1;
    text-align: center;
  }
  .section {
    line-height: 1.1;
    font-size: 1.3em;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS",
      sans-serif;
  }
  .firstText {
    font-size: 0.7em;
  }
  .secText {
    font-size: 0.6em;
  }
  .lorem p {
    font-size: 0.6em;
    opacity: 0.4;
  }
  .image {
    top: 68%;
    left: 0vw;
    width: 50%;
    justify-content: center;
  }
  .image img {
    width: 90%;
  }
  .bigImg {
    transform: translate(60%, 0%);
    width: 50%;
  }
  .bigImg img {
    width: 50vw;
    height: 35vh;
  }
}

@media only screen and (min-width: 350px) {
  .container {
    height: 35em;
  }
  .bigImg {
    margin-top: 5%;
  }
  .bigImg img {
    border-top-right-radius: 20px;
  }
}

@media only screen and (min-width: 470px) {
  .container {
    height: 50em;
  }
  .wrapper {
    height: inherit;
    flex-direction: column;
  }
  .leftSectionWrapper {
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  .section {
    line-height: 1.1;
    font-size: 3.5em;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS",
      sans-serif;
  }
  .firstText {
    font-size: 0.7em;
  }
  .secText {
    font-size: 0.6em;
  }
  .lorem p {
    font-size: 1.5em;
    opacity: 0.4;
  }
  .image {
    top: 68%;
    left: 0vw;
    width: 50%;
    justify-content: center;
  }
  .image img {
    width: 90%;
  }
  .bigImg {
    transform: translate(60%, 0%);
    width: 50%;
  }
  .bigImg img {
    width: 50vw;
    height: 35vh;
  }
}
@media only screen and (min-width: 800px) {
  .container {
    height: 40em;
  }
  .wrapper {
    align-items: center;
  }

  .leftSectionWrapper {
    justify-content: flex-start;
    flex: 1;
  }

  .section {
    font-size: 3em;
  }
  .lorem p {
    font-size: 17px;
  }
  .image {
    top: 52%;
  }
  .image img {
    height: 17em;
    padding: 3% 0 3% 0;
  }
  .bigImg {
    transform: translate(30%, -25%);
  }
  .bigImg img {
    height: 95%;
  }
}

@media only screen and (min-width: 1024px) {
  .container {
    height: 50em;
  }
  .bigImg {
    transform: translate(0%, -50%);
  }
}

@media only screen and (min-width: 1076px) {
  .container {
    height: 15em;
    transform: translate(0%, 10%);
  }
  .wrapper {
    flex-direction: row;
    align-items: flex-start;
    height: inherit;
  }
  .leftSectionWrapper {
    flex-direction: column;
    width: 50%;
    text-align: start;
    align-items: center;
    height: 270.59px;
  }
  .section {
    font-size: 13px;
    line-height: 1.5;
    height: 140px;
    width: 480px;
  }
  .firstText {
    font-size: 26px;
  }
  .secText {
    font-size: 25.4px;
  }
  .lastStrong {
    font-size: 20px;
    text-align: left;
  }
  .lorem {
    width: 480px;
    text-align: left;
  }
  .lorem p {
    opacity: 0.2;
    font-size: 13px;
    padding: 10;
  }

  .image {
    left: 48vw;
    top: -5%;
    justify-content: flex-start;
    width: 18%;
  }

  .image img {
    height: 30vh;
    width: 100%;
  }

  .bigImg {
    transform: translate(42%, -55%);
  }
  .bigImg img {
    background-color: rgb(0, 0, 255);
    border-radius: 30px;
    border-top-right-radius: 0;
    border-top-right-radius: 0;
    width: 30vw;
    height: 40vh;
  }
}
